import React from "react";
import {createBrowserRouter, RouterProvider} from 'react-router-dom';
import Navbar from './Navbar';
import Homepage from './Homepage';
import Signup from './Signup';
import Signin from './Signin';
import Dashboard from './Dashboard';
import Sidebar from './Sidebar';
import About from './About';
import VerifyEmail from './VerifyEmail';
import ResetPassword from "./ResetPassword";
import ChangePassword from "./ChangePassword";
import Error from './Error';
import Notifications from "./Notifications";
import Meetings from "./Meetings";
import Teams from "./Teams";
import NewScheduler from "./NewScheduler";
import SchedulerTemplate from "./SchedulerTemplate";
import ChatDashboard from "./ChatDashboard";
import Profile from './Profile';
import UpdateScheduler from './UpdateScheduler';
import GoogleSignIn from './GoogleSignIn'
import MeetingView from "./MeetingView";
import Contact from "./Contact";
import PrivacyPolicy from "./PrivacyPolicy";
import TermsAndConditions from "./TermsAndConditions";
import NotFound from "./NotFound";

const router = createBrowserRouter([
  {
    path: '/',
    element: <Navbar />,
    children: [
      {
        index: true,
        element: <Homepage />
      },
      {
        path: 'privacy-policy',
        element: <PrivacyPolicy />
      },
      {
        path: 'terms-and-conditions',
        element: <TermsAndConditions />
      },
      {
        path: 'get-started',
        element: <Signup />
      },
      {
        path: 'login',
        element: <Signin />
      },
      {
        path: 'contact',
        element: <Contact />
      },
      {
        path: 'verify/:code/:email',
        element: <VerifyEmail />
      },
      {
        path: 'forgot-password',
        element: <ResetPassword />
      },
      {
        path: 'change-password/:code/:email',
        element: <ChangePassword />
      },
      {
        path: 'error',
        element: <Error />
      },
      {
        path: '*',
        element: <NotFound />
      }
    ]
  },
  {
    path: 'app',
    element: <Sidebar />,
    children: [
      {
        path: 'setup',
        element: <GoogleSignIn />
      },
      {
        index: true,
        element: <Dashboard />
      },
      {
        path: 'notifications',
        element: <Notifications />
      },
      {
        path: 'teams',
        element: <Teams />
      },
      {
        path: 'meetings',
        element: <Meetings />
      },
      {
        path: 'profile',
        element: <About />
      },
      {
        path: 'meetings/scheduler/new',
        element: <NewScheduler />
      },
      {
        path: 'teams/:name/chat',
        element: <ChatDashboard />
      },
      {
        path: 'profile/:email',
        element: <Profile />
      },
      {
        path: 'meetings/scheduler/update/:name',
        element: <UpdateScheduler />
      },
      {
        path: 'meetings/:meetingId',
        element: <MeetingView />
      }
    ]
  },
  {
    path: 'schedule/:name',
    element: <SchedulerTemplate />
  }
])
function App() {
  return (
    <RouterProvider router={router} />
  )
}

export default App;
