import React, { useState, useLayoutEffect } from 'react';
import '../styles/timeSelector.css'
const TimeSelector  = ({time, setTime}) => {

    const [timeArray, setTimeArray] = useState(() => ['0:00','0:30','1:00','1:30','2:00','2:30','3:00','3:30','4:00','4:30','5:00','5:30','6:00','6:30','7:00','7:30','8:00','8:30','9:00','9:30','10:00','10:30','11:00','11:30','12:00','12:30','13:00','13:30','14:00','14:30','15:00','15:30','16:00','16:30','17:00','17:30','18:00','18:30','19:00','19:30','20:00','20:30','21:00','21:30','22:00','22:30','23:00','23:30'])
    const [options, setOptions] = useState(() => null);
    const [firstSelectedOption, setFirstSelectedOption] = useState(() => time[0]);
    const [secondSelectedOption, setSecondSelectedOption] = useState(() => time[1]);

    useLayoutEffect(() => {
        const timeList = timeArray.map(value => <option value={value} key={value}>{value}</option>)
        setOptions(timeList)
        setTime([firstSelectedOption, secondSelectedOption])
    }, [])

    const changeFirstSelectedOption = (e) => {
        setFirstSelectedOption(e.target.value)
        setTime(prev => {
            prev[0] = e.target.value;
            return [...prev];
        })
    }

    const changeSecondSelectedOption = (e) => {
        setSecondSelectedOption(e.target.value)
        setTime(prev => {
            prev[1] = e.target.value;
            return [...prev];
        })
    }

    return (
        <div className='time-selector-div'>
            <select className="form-select time-select custom-scroll-bar" value={firstSelectedOption} onChange={changeFirstSelectedOption} aria-label="Select Starting Time">
                {options}
            </select>
            <span className='mx-2'>To</span>
            <select className="form-select time-select custom-scroll-bar" value={secondSelectedOption} onChange={changeSecondSelectedOption} aria-label="Select Ending Time">
                {options}
            </select>
        </div>

    )
}

export default TimeSelector;